import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {DashboardComponent} from './pages/dashboard/dashboard.component';
import {VaultProfileSigninComponent} from './pages/vault-profile-signin/vault-profile-signin.component';
import {IsAuthenticatedAuthGuard} from './auth-guards/is-authenticated-auth-guard';
import {VaultProfileSigninCodeComponent} from './pages/vault-profile-signin-code/vault-profile-signin-code.component';
import {IdentityVerificationComponent} from './pages/identity-verification/identity-verification.component';

const routes: Routes = [

  { path: 'auth/signin', component: VaultProfileSigninComponent },
  { path: 'auth/signin/code', component: VaultProfileSigninCodeComponent },
  { path: 'auth/identity-verification', component: IdentityVerificationComponent },

  { path: '', redirectTo: '/auth/signin', pathMatch: 'full' },
  { path: 'dashboard', component: DashboardComponent, canActivate: [ IsAuthenticatedAuthGuard] },

  //must be at bottom of list
  { path: '**', redirectTo: 'auth/signin' },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
